import { round } from 'lodash'
import React from 'react'

const MainTableCoin = (props) => {
  return (
    <div className={"main-table-coin-4"} style={{width: "100%", overflow: "auto"}}>
        <table className={"main-table-table-coin-4"} style={{width: "100%"}}>
            <Thead />
            <Tbody data={props.data} />
        </table>
    </div>
  )
}

export default MainTableCoin

const Thead= (props)=> {
    return (
        <thead className={"main-table-thead-coin-4"}>
            <tr>
                <th>Exchange</th>
                <th>Symbol</th>
                {/* <th>Quote</th> */}
                {/* <th>Base</th> */}
                <th>Buy</th>
                <th>Sell</th>
                <th>Total</th>
                {/* <th>Diff</th> */}
                {/* <th>Total % 24H V</th> */}
                {/* <th>Vol</th> */}
            </tr>
        </thead>
    )
}

const Tbody= (props)=> {
    return (
        <tbody className={"main-table-tbody-coin-4"}>
            {
                 props?.data && props?.data?.map((item, key)=> <tr key={key}>
                    <td>{item.exchange}</td>
                    <td>{item.market}</td>
                    {/* <td>{item.pair}</td> */}
                    {/* <td>{item.market.slice(0, 3)}</td> */}
                    <td>{round((item?.m5_stats?.buy || 0), 2)}</td>
                    <td>{round((item?.m5_stats?.sell || 0), 2)}</td>
                    {/* <td>{round((parseFloat(item?.m5_stats?.buy) || 0) + (parseFloat(item?.m5_stats?.sell) || 0), 2)}</td> */}
                    <td>{round((parseFloat(item?.m5_stats?.buy) || 0) - (parseFloat(item?.m5_stats?.sell) || 0))}</td>
                    {/* <td>{round((((parseFloat(item?.m5_stats?.buy) || 0) + (parseFloat(item?.m5_stats?.sell) || 0)) / item?.vol * 100), 2)}</td> */}
                    {/* <td>{item.vol?.toFixed(2)}</td> */}
                </tr>)

            }
        </tbody>
    )
}